import { Box, Button, Container, Grid, TextField, TextareaAutosize } from "@mui/material";
import { useState } from "react";

import './AddInspect.css';
import TableSelect from "./TableSelect";
import { BACKENDURL } from "../Helper/config";
import { useNavigate } from "react-router-dom";

function AddInpect() {
    const [form, setForm] = useState({
        inspection_detail: []
    });
    const [insDetail, setInsDetail] = useState({
        row: "",
        col: ""
    })
    const [select, setSelect] = useState(false);
    const [selected, setSelected] = useState(false);
    const nevigate = useNavigate();

    function setDimention() {
        const array = [];
        for (let i = 1; i <= insDetail.row; i++) {
            array.push([]);
            for (let j = 1; j <= insDetail.col; j++) {
                console.log(document.getElementById(`${i}-${j}`)?.value);
                array[i - 1].push("");
            }
        }
        form.inspection_detail = array;
        setForm({ ...form });
        console.log(form.inspection_detail);
    }
    function handleChange(e) {
        setForm({
            ...form,
            [e.target.name]: e.target.value
        });
    }
    function handleDetailChange(e) {
        if (e.target.value.length > 2) {
            e.target.value = e.target.value.slice(0, 2);
            return;
        };
        const [i, j] = e.target.id.split('-');
        form.inspection_detail[i][j] = e.target.value;
        setForm({ ...form });
    }
    function submitForm(e) {
        e.preventDefault();
        console.log(form);
        fetch(BACKENDURL + 'inspection', {
            method: 'POST', headers: {
                'Content-Type': 'application/json'
            }, body: JSON.stringify(form)
        })
            .then(res => res.json())
            .then((res)=>{
                nevigate('/');
            
            });
    }
    return (
        <Container maxWidth="xs">
            <h1>Create Inspection</h1>
            <TextField
                fullWidth
                name="inspector"
                onChange={handleChange}
                value={form.inspector}
                label="inspector"
            />
            <Box padding={1}></Box>
            <TextField
                fullWidth
                name="template_name"
                onChange={handleChange}
                value={form.template_name}
                label="template name"
            />
            <Box padding={1}></Box>
            <TextField
                fullWidth
                name="industry_type"
                onChange={handleChange}
                value={form.industry_type}
                label="industry type"
            />
            <Box padding={1}></Box>

            <textarea
                rows={5}
                style={{ width: "100%" }}
                name="description"
                onChange={handleChange}
                value={form.description}
                label="Description"
                placeholder="Description"
            />
            <Box padding={1}></Box>

            <TextField
                fullWidth
                name="craeted_at"
                type="date"
                onChange={handleChange}
            />
            <Box padding={1}></Box>
            <TextField
                fullWidth
                name="location"
                type="text"
                label="location"
                onChange={handleChange}
            />
            <Box padding={1}></Box>


            {!selected && <>
                <Button
                    onClick={() => setSelect(true)}
                    variant="outlined"
                    fullWidth
                >
                    select table
                </Button>
            </>
            }
            {selected &&
                <div className="center-div">
                    <div>
                        Detailed Info
                        <table>
                            <tbody>
                                {
                                    form.inspection_detail.map((cols, i) => (
                                        <tr key={i} >
                                            {
                                                cols.map((_, j) => (
                                                    <td key={j}>
                                                        <TextField
                                                            className="table-input"
                                                            id={`${i}-${j}`}
                                                            name={`${i}-${j}`}
                                                            // helperText={`${i}-${j}`}
                                                            onChange={handleDetailChange}
                                                        />
                                                    </td>
                                                ))
                                            }
                                        </tr>
                                    ))
                                }
                            </tbody>
                        </table>
                    </div>
                </div>
            }

            <Box padding={1}></Box>
            <Box className='submit-btn'>
                <Button
                    variant="contained"
                    onClick={submitForm}
                    sx={{ width: "50%" }}
                >Save</Button>
            </Box>
            {select &&
                <TableSelect
                    setDimention={setDimention}
                    setSelect={setSelect}
                    setSelected={setSelected}
                    insDetail={insDetail}
                    setInsDetail={setInsDetail}
                />
            }
        </Container>
    );
}

export default AddInpect;
