import { Route, Routes, BrowserRouter } from 'react-router-dom'
import ListInspect from './pages/ListInspect';
import AddInpect from './pages/AddInpect';
import EditInspect from './pages/EditInspect';
function App() {
  return (
    <BrowserRouter>
      <Routes>
        <Route path="/" element={<ListInspect />} />
        <Route path="/add" element={<AddInpect />} />
        <Route path="/edit/:id" element={<EditInspect />} />
      </Routes>
    </BrowserRouter>
  );
}

export default App;
