import { Box, Button, Grid, TextField } from "@mui/material";

const TableSelect = (props) => {
    const sizeLimit = 20;
    function changeDimantion(e) {
        if (e.target.value < 0) return;
        if (e.target.value > sizeLimit) return;
        props.insDetail[e.target.name] = e.target.value;
        props.setInsDetail({ ...props.insDetail });
        handleMouseOverEvent2(props.insDetail.row, props.insDetail.col)
    }
    function setDimentionByClick() {
        props.setDimention();
        props.setSelect(false)
        props.setSelected(true)
    }
    function handleMouseOverEvent(e) {
        var [i, j] = e.target.id.split('-');
        i = parseInt(i);
        j = parseInt(j);
        props.setInsDetail({
            row: i + 1,
            col: j + 1
        })
        handleMouseOverEvent2(i, j)
    }
    function handleMouseOverEvent2(i, j) {
        try {

            for (let x = 0; x <= i; x++) {
                for (let y = 0; y <= j; y++) {
                    document.getElementById(`${x}-${y}`).style.border = "1px solid red";
                }
            }
            for (let x = i + 1; x < sizeLimit; x++) {
                for (let y = 0; y < sizeLimit; y++) {
                    document.getElementById(`${x}-${y}`).style.border = "1px solid darkgrey";
                }
            }
            for (let x = 0; x < sizeLimit; x++) {
                for (let y = j + 1; y < sizeLimit; y++) {
                    document.getElementById(`${x}-${y}`).style.border = "1px solid darkgrey";
                }
            }
        } catch (e) {
            console.log(e);
        }
    }
    return (
        <div className="popupdiv">
            <div>
                <Box sx={{ pb: 2 }}>
                    Select Dimention
                </Box>
                <Grid container maxWidth={500} className="small-container">
                    <Grid children sm={6} xs={6}>
                        <Box padding={1}>
                            <TextField
                                fullWidth
                                size="small"
                                name="row"
                                onChange={changeDimantion}
                                type="number"
                                value={props.insDetail.row}
                                label="Row"
                            />
                        </Box>
                    </Grid>
                    <Grid children sm={6} xs={6} >
                        <Box padding={1}>
                            <TextField
                                fullWidth
                                name="col"
                                size="small"
                                onChange={changeDimantion}
                                type="number"
                                value={props.insDetail.col}
                                label="Col"
                            />
                        </Box>
                    </Grid>
                    <Grid children sm={12} xs={12} className="center-div">
                        <table>
                            <tbody>
                                {
                                    Array.from({ length: sizeLimit }).map((_, i) => (
                                        <tr key={i} >
                                            {
                                                Array.from({ length: sizeLimit }).map((_, j) => (
                                                    <td key={j}>
                                                        <div
                                                            className="table-cell"
                                                            id={`${i}-${j}`}
                                                            onMouseOver={(e) => handleMouseOverEvent(e)}
                                                            onClick={setDimentionByClick}
                                                        >
                                                        </div>
                                                    </td>
                                                ))
                                            }
                                        </tr>
                                    ))
                                }
                            </tbody>
                        </table>
                    </Grid>
                    <Grid children sm={12} >
                        <div className="submit-btn">
                            <Button
                                fullWidth
                                variant="contained"
                                onClick={setDimentionByClick}
                            >
                                Done
                            </Button>
                        </div>
                    </Grid>
                </Grid>
            </div>

        </div>
    )
}

export default TableSelect;