import { DataGrid } from '@mui/x-data-grid';
import { useEffect, useState } from 'react';
import { BACKENDURL } from '../Helper/config';
import { Box, Button, Container } from '@mui/material';
import { Link } from 'react-router-dom';

const ListInspect = () => {
    const [inspections, setInspections] = useState([]);

    const columns = [
        { field: '_id', headerName: 'ID', width: 200 },
        { field: 'template_name', headerName: 'Template Name', width: 200 },
        { field: 'industry_type', headerName: 'Industry Type', width: 200 },
        { field: 'craeted_at', headerName: 'Craeted At', width: 150 },
        { field: 'inspector', headerName: 'inspector', width: 200 },
        {
            headerName: 'Action', renderCell: (params) => (
                <Link to={'/edit/' + params.row._id}>
                    <Button
                        variant='contained'
                    >
                        Update
                    </Button>
                    sdsd
                </Link>
            ),
        }
    ];
    useEffect(() => {
        fetch(BACKENDURL + 'inspection/')
            .then(res => res.json())
            .then(res => setInspections(res));
    }, [])
    return (
        <Container maxWidth="lg">
            <Box sx={{ display: 'flex', justifyContent: 'flex-end', marginBottom: '20px' }}>
                <Link to="/add">
                    <Button variant='contained'>Create Inspection</Button>
                </Link>
            </Box>
            <DataGrid rows={inspections} columns={columns} getRowId={(row) => row._id} />
        </Container>
    )
}

export default ListInspect;